import React, { useEffect, useState } from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie } from 'react-chartjs-2';
import { adminService } from '../../services/admin.service';
import {Loader} from '../loader'

ChartJS.register(ArcElement, Tooltip, Legend);




export function PieChart() {

  const [doctors,setDoctors] = useState(null)

  useEffect( () => {
    getDoctors();
 }, []);

const getDoctors = async () =>{
    const doctors = await adminService.getDoctors();
    setDoctors(prevDoctors => prevDoctors = doctors)
}

const data = {
  labels: !doctors ? [] : doctors.map(doctor => doctor.fullname),
  datasets: [
    {
      label: '# of Votes',
      data: !doctors ? [] : doctors.map(doctor => doctor.patients.length),
      backgroundColor: [
        'rgba(255, 99, 132, 0.2)',
        'rgba(54, 162, 235, 0.2)',
        'rgba(75, 192, 192, 0.2)',
        'rgba(255, 206, 86, 0.2)',
        'rgba(255, 159, 64, 0.2)',
        'rgba(153, 102, 255, 0.2)',
      ],
      borderColor: [
        'rgba(255, 99, 132, 1)',
        'rgba(54, 162, 235, 1)',
        'rgba(75, 192, 192, 1)',
        'rgba(255, 159, 64, 1)',
        'rgba(255, 206, 86, 1)',
        'rgba(153, 102, 255, 1)',
      ],
      borderWidth: 1,
    },
  ],
};
  if(!doctors) return <Loader/>
  return <div className='pie-continer'><h1>doctors by patients </h1><div className='pie'> <Pie  data={data} /> </div></div>;
}
